import RightEmployee from "./rightEmployee";
import Search from '@/components/search';
import { GetDevicesByGroupId, GroupGetById } from '@/api/deviceGroup';
import { getDevice, deleteDevice } from '@/api/devicelist';
import Detail from "./details";
import { pagging, buttonPermissions } from '@/mixin';
import BindView from "./bindView";
export default {
  name: 'List',
  components: {
    Search: Search,
    Detail: Detail,
    RightEmployee: RightEmployee,
    BindView: BindView
  },
  mixins: [pagging, buttonPermissions],
  data: function data() {
    return {
      loading: false,
      rightShow: false,
      tableData: [],
      detailShow: false,
      cardTitle: '',
      str: '',
      listStr: '',
      addStr: '',
      partStr: '',
      positionStr: '',
      monitorStr: '',
      viewShow: false,
      addDeviceStr: "",
      fixProperties: '123'
    };
  },
  mounted: function mounted() {
    this.init();

    if (this.$store.state.user.config.isPro) {
      this.str = '/ExpertSystem/FaultManagement/analysisDetail1';
      this.listStr = '/ExpertSystem/EquipmentManagement/status/';
      this.addStr = '/ExpertSystem/EquipmentManagement/add';
      this.partStr = '/ExpertSystem/EquipmentManagement/partDevice';
      this.positionStr = '/ExpertSystem/EquipmentManagement/location';
      this.monitorStr = '/ExpertSystem/EquipmentManagement/monitor', this.addDeviceStr = '/ExpertSystem/EquipmentManagement/addPartsFromTemplate';
      this.performanceStr = '/ExpertSystem/EquipmentManagement/performance';
    } else {
      this.str = '/FaultManagement/analysisDetail1';
      this.listStr = '/EquipmentManagement/status/';
      this.addStr = '/EquipmentManagement/add';
      this.partStr = '/EquipmentManagement/partDevice';
      this.positionStr = '/EquipmentManagement/location';
      this.monitorStr = '/EquipmentManagement/monitor';
      this.addDeviceStr = '/EquipmentManagement/addPartsFromTemplate';
      this.performanceStr = '/EquipmentManagement/performance';
    }
  },
  methods: {
    menuPermissions: function menuPermissions(code) {
      return this.$store.state.user.userInfo.menus.some(function (item) {
        return item.code === code;
      });
    },
    rightHidden: function rightHidden() {
      this.rightShow = false;
    },
    handleClickRight: function handleClickRight(id) {
      var _this = this;

      this.rightShow = true;
      this.$nextTick(function () {
        _this.$refs.rightDialog.init(id);
      });
    },
    changeKeyword: function changeKeyword(val) {
      this.keyword = val;
    },
    searchInit: function searchInit() {
      this.pageIndex = 1;
      this.init();
    },
    init: function init() {
      var _this2 = this;

      this.loading = true;

      if (this.$route.params.groupId) {
        GetDevicesByGroupId({
          groupId: this.$route.params.groupId,
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        }).then(function (res) {
          _this2.loading = false;

          _this2.$set(_this2, 'tableData', res.data.items);

          _this2.totalCount = res.data.totalCount;
          _this2.pageIndex = res.data.pageIndex;
          _this2.pageSize = res.data.pageSize;
        });
        GroupGetById(this.$route.params.groupId).then(function (res) {
          _this2.cardTitle = res.data.groupName;
        });
      } else {
        getDevice({
          keyword: this.keyword,
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        }).then(function (res) {
          _this2.loading = false;

          _this2.$set(_this2, 'tableData', res.data.items);

          _this2.totalCount = res.data.totalCount;
          _this2.pageIndex = res.data.pageIndex;
          _this2.pageSize = res.data.pageSize;
        });
      }
    },
    insert: function insert() {
      var _this3 = this;

      this.detailShow = true;
      this.$nextTick(function () {
        _this3.$refs.detail.init();
      });
    },
    editHidden: function editHidden() {
      this.detailShow = false;
      this.init();
    },
    edit: function edit(id) {
      var _this4 = this;

      this.detailShow = true;
      this.$nextTick(function () {
        _this4.$refs.detail.init(id);
      });
    },
    del: function del(id) {
      var _this5 = this;

      this.$confirm(this.$t('list.deleteDevice'), this.$t('tips'), {
        confirmButtonText: this.$t('ok'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(function () {
        deleteDevice(id).then(function () {
          _this5.$message({
            type: 'success',
            message: _this5.$t('rules.successfulDeletion'),
            duration: 500,
            onClose: function onClose() {
              _this5.pageIndex = 1;

              _this5.init();
            }
          });
        });
      }).catch(function () {
        _this5.$message({
          type: 'info',
          message: _this5.$t('rules.canceledDeletion')
        });
      });
    },
    bindView: function bindView(row) {
      var _this6 = this;

      // 绑定视图
      this.viewShow = true;
      this.$nextTick(function () {
        _this6.$refs.bindView.init(row);
      });
    }
  }
};