export default {
  name: 'FixAttr',
  props: ['properties', 'propertiesName'],
  computed: {
    dynamicValidateForm: function dynamicValidateForm() {
      return {
        dynamicValidateForm: this.domains
      };
    }
  },
  data: function data() {
    return {
      domains: [{
        propertyName: '行业',
        propertyValue: '传统行业'
      }]
    };
  },
  watch: {
    properties: function properties(newVal) {
      this.init(newVal);
    }
  },
  mounted: function mounted() {},
  methods: {
    init: function init() {
      for (var i in this.properties) {
        if (this.properties[i].isShow == 0) {
          this.properties[i].isShow = false;
        } else if (this.properties[i].isShow == 1) {
          this.properties[i].isShow = true;
        }

        for (var y in this.propertiesName) {
          if (this.propertiesName[y].propertyName == this.properties[i].propertyName) {
            this.propertiesName[y] = this.properties[i];
          }
        }
      }
    }
  }
};