// import AMap from 'AMap'
export default {
  name: 'Position',
  props: ['formAddress'],
  data: function data() {
    return {
      valuePosition: '',
      marker: null,
      map: null,
      lng: '',
      lat: '',
      mapClickFn: false,
      address: null
    };
  },
  methods: {
    getAddress: function getAddress() {
      this.initMap();
      this.mapTip();
      this.mapClick();
    },
    mapClick: function mapClick() {
      var _this = this;

      if (!this.mapClickFn) {
        this.mapClickFn = true;
        this.map.on('click', function (e) {
          _this.lng = e.lnglat.lng;
          _this.lat = e.lnglat.lat;

          _this.positionMthods([_this.lng, _this.lat]);

          if (_this.marker) {
            _this.map.remove(_this.marker);
          } // eslint-disable-next-line no-undef


          _this.marker = new AMap.Marker({
            // eslint-disable-next-line no-undef
            position: new AMap.LngLat(_this.lng, _this.lat)
          });

          _this.map.add(_this.marker);
        });
      }
    },
    initMap: function initMap() {
      // 地图加载
      if (!this.map) {
        // eslint-disable-next-line no-undef
        this.map = new AMap.Map(this.$refs.container, {
          resizeEnable: true,
          zoom: 12,
          center: [116.397428, 39.90923]
        });
      }
    },
    mapTip: function mapTip() {
      var _this2 = this;

      // 输入提示
      var autoOptions = {
        city: '',
        input: 'tipinput',
        output: 'leftBox'
      };
      this.map.plugin(['AMap.Autocomplete', 'AMap.PlaceSearch'], function () {
        // eslint-disable-next-line no-undef
        var auto = new AMap.Autocomplete(autoOptions); // eslint-disable-next-line no-undef

        var placeSearch = new AMap.PlaceSearch({
          map: _this2.map
        }); // 构造地点查询类
        // eslint-disable-next-line no-undef

        AMap.event.addListener(auto, 'select', select); // 注册监听，当选中某条记录时会触发

        function select(e) {
          placeSearch.setCity(e.poi.adcode);
          placeSearch.search(e.poi.name); // 关键字查询查询
        }

        _this2.mapClick(); // POI后生成的marker的点击事件
        // eslint-disable-next-line no-undef


        AMap.event.addListener(placeSearch, 'markerClick', function (e) {
          // const location = e.data.location
          // this.$emit('setPosition', { lng: location.lng, lat: location.lat, pri: e.data.pname, city: e.data.cityname })
          _this2.valuePosition = e.data.name;
        });
      });
    },
    positionMthods: function positionMthods(lnglat) {
      var _this3 = this;

      if (typeof lnglat == "string") {
        this.valuePosition = lnglat;
      } else {
        // 逆地理编码
        // eslint-disable-next-line no-undef
        AMap.plugin('AMap.Geocoder', function () {
          // eslint-disable-next-line no-undef
          var geocoder = new AMap.Geocoder({
            // city 指定进行编码查询的城市，支持传入城市名、adcode 和 citycode
            city: '全国'
          });
          geocoder.getAddress(lnglat, function (status, result) {
            if (status === 'complete' && result.info === 'OK') {
              var address = {
                province: result.regeocode.addressComponent.province,
                city: result.regeocode.addressComponent.city
              };

              _this3.$set(_this3, 'address', address);

              _this3.valuePosition = result.regeocode.formattedAddress; // this.$emit('setPosition', { lng: lnglat[0], lat: lnglat[1], ...this.address })
            }
          });
        });
      }
    }
  }
};